<template>
  <canvas ref="myChart"></canvas>
</template>

<script>
//chart  -> plugin  chartPlugin.js 
//main.js  -> mounted  ->  

export default {
  mounted() {
    new this.ChartJS(this.$refs.myChart, {
        // The type of chart we want to create
        type: 'polarArea',

        // The data for our dataset
        data: {
            labels: ["figure1", "figure2", "figure3", "figure4", "figure5"],
            datasets: [{
                label: "Nutrient",
                backgroundColor: [
                    '#f1c40f', '#e67e22', '#16a085', '#2980b9','#8e44ad'
                ],
                data: [10,20,30,40,60,50],
            }]
        },

        // Configuration options go here
        options: {
            animation : {
                animateRotate : true,
                animateScale : true
            },
            responsive : true
        }
    });
  }
}
</script>